
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: #7e8c8d;
  -webkit-backface-visibility: hidden;
  text-decoration: none;
}
li {
  list-style: none;
}
body {
  -webkit-text-size-adjust: none;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
#app .container .weui-cell_access.active {
  background: none;
}
#app .container .weui-cell {
  padding: 1.8rem 1.6rem;
  box-sizing: border-box;
}
#app .container .weui-cell .vux-label {
  font-size: 1.4rem;
  color: #08090a;
  height: 1.4rem;
}
#app .container .weui-cell .vux-label-desc {
  font-size: 1.2rem;
  height: 1.2rem;
  color: #75787f;
  margin-top: 0.8rem;
  vertical-align: sub;
}
#app .container .weui-cell .weui-cell__ft {
  font-size: 1.2rem;
  padding-right: 1.5rem;
  color: #75787f;
  line-height: 1.2rem;
}
#app .container .vux-actionsheet-menu-default,
#app .container .weui-actionsheet__cell {
  padding: 1.6rem 0;
  font-family: PingFangSC;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
}
#app .container .weui-cells.vux-no-group-title:first-child {
  margin-top: 0;
}
#app .container .vux-popup-picker-header {
  color: #f00;
}
#app .container .vux-x-input.weui-cell {
  height: 5rem;
  line-height: 5rem;
}
#app .container .vux-x-input.weui-cell .weui-cell__bd.weui-cell__primary {
  height: 1.4rem;
  line-height: 1.4rem;
}
#app .container .vux-x-input.weui-cell .weui-cell__bd.weui-cell__primary .weui-input {
  height: 1.4rem;
  line-height: 1.4rem;
  font-size: 1.4rem;
}
#app .container .vux-tab {
  height: 4.1rem;
}
#app .container .vux-tab .vux-tab-item {
  height: 4.1rem;
  font-size: 1.4rem;
}
#app .container .classError .weui-cell__ft {
  color: #ff3340;
}
#app .container .classProgress .weui-cell__ft {
  color: #75787f;
}
#app .container .classSuccess .weui-cell__ft {
  color: #51cc68;
  padding-right: 0;
}
#app .container .weui-label {
  font-size: 1.4rem;
}
#app .container .weui-label .vux-popup-picker-value.vux-cell-value {
  font-size: 1.4rem;
}
#app .container .boxContainer:nth-child(2) .weui-cells {
  margin-top: 0;
}
#app .container .boxContainer .weui-cells {
  margin-top: 0.8rem;
}
#app .container .vux-popup-picker-select {
  font-size: 1.4rem;
  line-height: 1.4rem;
}
#app .container .vux-cell-value {
  color: #08090a;
}
#app .container .weui-switch:checked,
#app .container .weui-switch-cp__input:checked ~ .weui-switch-cp__box {
  border-color: #ff3340;
  background-color: #ff3340;
}
.dp-container .dp-header .dp-item {
  font-size: 1.6rem;
}
.dp-container .dp-header .dp-item.dp-right {
  color: #ff3340;
}
.dp-container .dp-header .dp-item.dp-left {
  color: #08090a;
}
.weui-dialog .weui-dialog__title {
  font-size: 2rem;
  font-weight: 300;
  color: #08090a;
}
.weui-dialog .weui-dialog__hd {
  padding: 2.8rem 0 1.6rem 0;
}
.weui-dialog .weui-dialog__bd {
  font-size: 1.4rem;
  color: #75757f;
  padding-bottom: 2rem;
  line-height: 2.2rem;
}
.weui-dialog .weui-dialog__btn {
  font-size: 1.6rem;
  font-weight: 300;
  padding: 1.7rem 0;
  line-height: 1.6rem;
}
.weui-dialog .weui-dialog__btn_primary {
  color: #ff3340;
}
.weui-dialog .weui-dialog__btn:after {
  height: 50%;
  top: 25%;
}
.vux-popup-picker-container .vux-popup-picker-header {
  color: #ff3340;
  font-size: 1.6rem;
}
.vux-popup-picker-container .scroller-item {
  font-size: 1.6rem;
  font-weight: 300;
}
.weui-toast {
  font-size: 1.2rem;
}
.vux-toast .vux-toast-bottom .weui-toast__content {
  font-size: 1rem;
}
#app {
  position: fixed;
  width: 100%;
  height: 100%;
}
#app .container {
  height: 100%;
  width: 100%;
  overflow: hidden;
}


